body {
    min-width: 320px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-family: $font-text;
    color: $color-text;
    font-size: 16px;
    cursor: url(/src/images/cursor.png), auto;
    background-color: #FFFFF9;
    
 background-image: url(../images/header/donuts-mobile@1.png), url(../images/top-bg-tel.png), url(../images/bot-bg-tel.png);
 background-repeat: no-repeat, no-repeat, no-repeat;
 background-position: top 0 left 0, top, bottom;
 background-size: 130px, 100% 460px, 100% 1337px;
 @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image:
    url(../images/header/donuts-mobile@2.png), url(../images/top-bg-tel@2.png), url(../images/bot-bg-tel@2.png);
      background-repeat: no-repeat, no-repeat, no-repeat;
      background-position: top 0 left 0, top, bottom;
      background-size: 130px, 100% 460px, 100% 1337px;
  }

  @media screen and (max-width: 319px) {
    background-size: 130px, cover, cover;
  }

    @media screen and (min-width: 768px) {
        background-size: 430px, 100% 622px, 100% 1512px;
        background-image:
        url(../images/header/donuts-tablet@1.png), url(../images/top-bg-tab.png), url(../images/bot-bg-tab.png);
        background-repeat: no-repeat, no-repeat, no-repeat;
        background-position: top 0 right 90px, top, bottom;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image:
        url(../images/header/donuts-tablet@1.png), url(../images/top-bg-tab@2.png), url(../images/top-bg-tab@2.png);
        background-size: 430px, 100% 622px, 100% 1512px;
        background-repeat: no-repeat, no-repeat, no-repeat;
        background-position: top 0 right 90px, top, bottom;
        }
    }

    @media screen and (min-width:1280px) {
        font-size: 18px;
        background-size: 660px, 100% 956px, 100% 1931px;
        background-image:
        url(../images/header/donuts-desktop@1.png), url(../images/top-bg.png), url(../images/bot-bg.png);
        background-repeat: no-repeat, no-repeat, no-repeat;
        background-position: top 0 right 0, top, bottom;
    
        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image:
          url(../images/header/donuts-desktop@1.png), url(../images/top-bg@2.png), url(../images/bot-bg@2.png);
          background-size: 660px, 100% 956px, 100% 1931px;
          background-repeat: no-repeat, no-repeat, no-repeat;
          background-position: top 0 right 0, top, bottom;
        }
    }
}



a {
    cursor: url(/src/images/tap.png), pointer;
}



button {
    cursor: url(/src/images/tap.png), pointer;
}