.registration {
    padding-top: 32px;
        @media screen and (min-width: 768px) {
            padding-top: 40px;
        }
            @media screen and (min-width:1280px) {
                padding-top: 60px;
            }  
}
.img-box {
    position: relative;
}
.thumb {
    padding: 32px 8px 32px 8px;
    border-radius: 150px 150px 0px 0px ;
    background-color: #FFFFF9;
    box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15),
     4px 4px 4px rgba(171, 129, 108, 0.15); 
       @media screen and (min-width: 768px) {
            position: relative;
            display: flex;
            justify-content: center;
            margin-left: auto;
            margin-right: auto;
            max-width: 704px;
            height: 260px;
            border-radius: 150px;
            z-index: 2;
        }
            @media screen and (min-width:1280px) {
                margin-left: auto;
                margin-right: auto;
                max-width: 822px;
            }
}

.img-box__donut {
   display: none;

        @media screen and (min-width:768px) {
            display: block;
            position: absolute;
            left: -140px;
            top: 85px;
            transform: translate(50%, -50%);
        }
            @media screen and (min-width:1280px) {
                top: 0px;
                left: 83px;
                transform: rotate(-15deg);
            }
    
    
}
.img-box__confetti {
        display: none;

        @media screen and (min-width:768px) {
            display: block;
            position: absolute;
            right: -15px;
            bottom: -145px;
           
        }
            @media screen and (min-width:1280px) {
                right: 0;
                bottom: -295px;
            }
    
} 


.thumb__border {
    margin-bottom: 8px;
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(171, 129, 108, 0.5);
        @media screen and (min-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-around;
            margin: 0;
            padding: 0;
            padding-right: 32px;
            margin-right: 32px;
            border: none;
            border-right: 1px solid rgba(171, 129, 108, 0.5);
        }
        @media screen and (min-width:1280px) {
            display: block;
            padding-top: 23px;
        }
}
.registration__title {
    font-family:$font-title;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.25;
    text-align: center;
    color: #2E2520;
        @media screen and (min-width: 768px) {
            display: none;
        }
}
.registration__title--tablet {
    display: none;
    font-family: $font-title;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.25;
    text-align: center;
    color: #2E2520;
        @media screen and (min-width: 768px) {
            display: block;
            margin-bottom: 32px;
        }
        @media screen and (min-width:1280px) {
            font-size: 36px;
            line-height: 1.8;
            margin-bottom: 37px;
        }

}
.registration__text {
    font-family: $font-text;
    width: 114px;
    margin: 0 auto;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    color: #2E2520;
        @media screen and (min-width:768px) {
            width: 90px;
            text-align: right;
        }
        @media screen and (min-width:1280px) {
            width: 127px;
            font-size: 20px;
            line-height: 1.2;
            margin-bottom: 32px;
        }
}
.registration__price {
    display: block;
    font-family: $font-text;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    color: #2E2520;
        @media screen and (min-width:1280px) {
            font-size: 20px;
            line-height: 1.2;
            text-align: end;
        }
}
.form {
    @media screen and (min-width:768px) {
        display: flex;
    align-items: center;
    }
}

.form__title {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    max-width: 219px;
    font-family: $font-text;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;
    text-align: center;
    color: #2E2520;
        @media screen and (min-width:768px) {
            margin-bottom: 16px;
            margin-left: 0;
            text-align: left;
        }
            @media screen and (min-width:1280px) {
                font-size: 18px;
                line-height: 1.22;
                margin-bottom: 13px;
            }
    }
    .form__inner {
        @media screen and (min-width:768px) {
            display: flex;
            flex-direction: column;
            margin-right: 54px;
        }
        @media screen and (min-width:1280px) {
            margin-right: 32px;
        }
    }
    .form__input {
        width: 265px;
        color: #ab816c;
        border: none;
        outline: none;
        border-bottom: 1px solid #AB816C;
        margin-bottom: 22px;
        background-color: transparent;
        &--email {
            margin-bottom: 32px;
        }
            @media screen and (min-width:768px) {
                margin-bottom: 16px;
                width: 237px;
                &--email {
                    margin-bottom: 0;
                }
            }
            @media screen and (min-width:1280px) {
                margin-bottom: 15px;
                &--email {
                    margin-bottom: 0;
                }
            }
    }
    .form__input:hover {
        border-bottom-color: rgba(171, 129, 108, 0.5);
    }
    .form__input:hover.form__input::placeholder,
    .form__input:focus.form__input::placeholder
     { 
        color:#AB816C;   
    }
  .form__input:invalid {
      border-color: red;
    }

    .form__input::placeholder {
        font-family: $font-text;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.25;
        color: rgba(171, 129, 108, 0.5);
    }
    .form__button {
        display: block;
        margin: 0 auto;
        width: 145px;
        height: 40px;
        border-radius: 25px;
        border: none;
        font-family: $font-title;
        font-size: 18px;
        line-height: 1.27;
        background-color: #AB816C;
        color: white;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        color 250ms cubic-bezier(0.4, 0, 0.2, 1);
            @media screen and (min-width:1280px) {
                font-size: 24px;
                line-height: 1.25;
            }
            &:hover {
                background-color: rgba(171, 129, 108, 0.5);
                color: $color-button-bg;
                }
                &:focus {
            
                background-color: $color-white;
                border: 4px solid $color-button-bg;
                border-radius: 50%;
            
                    color: #ab816c;
                    background-color: #ab816c80;
                }
                &:active {
                    background-color: #6d6b69;
                    color: #FEF3E4;
            
                }
    }